import React from 'react';
import {Checkbox, FormControlLabel} from "@material-ui/core";
import './applicant.style.css'
import {useDispatch, useSelector} from "react-redux";
import AddApplicant from "./components/AddApplicant";
import {arrayIndexApplicantAction, deleteApplicant} from "../../store/actions";
import Submit from "../submitButtom/Submit";
import {makeStyles} from "@material-ui/styles";


const useStyles = makeStyles({
    title : {
        marginBottom : '15px'
    },
    root: {
        display : 'flex',
        marginBottom : '20px',
        ['@media (min-width:768px)']: {
            marginBottom : '0'
        }
    },
});

// ĞĞ°ÑÑĞ¸Ğ² Ğ¸Ğ½Ğ´ĞµĞºÑĞ¾Ğ² Ñ Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»ÑĞ¼Ğ¸
const checkIndex = []

// ĞĞ¾Ğ¼Ğ¿Ğ¾Ğ½ĞµĞ½Ñ Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»ĞµĞ¹
const Applicants = () => {

    // ĞĞ¾Ğ»ÑÑĞµĞ½Ğ¸Ñ Ğ¾Ğ±ÑĞµĞºÑĞ° Ğ²ÑĞ±ÑĞ¾Ğ½Ğ¾Ğ³Ğ¾ ĞºĞ»Ğ¸ĞµĞ½ÑĞ°
    const client = useSelector(({client}) => client)
    // ĞĞ¾Ğ»ÑÑĞµĞ½Ğ¸Ğµ Ğ¼Ğ°ÑÑĞ¸Ğ² Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»ĞµĞ¹
    const applicants = useSelector(({getApplicants}) => getApplicants || [])
    const dispatch = useDispatch()
    const styles = useStyles()

    // Ğ£Ğ´Ğ°Ğ»ĞµĞ½Ğ¸Ğµ Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»Ñ
    const handleDelete = (id, index) => {
        // Ğ£Ğ´Ğ°Ğ»ĞµĞ½Ğ¸Ğµ Ñ Ğ¼Ğ°ÑÑĞ¸Ğ²Ğ° Ğ¸Ğ½Ğ´ĞµĞºÑĞ¾Ğ²
        const elemIndex = checkIndex.indexOf(index)
        checkIndex.splice(elemIndex, 1)

        dispatch(arrayIndexApplicantAction(checkIndex))
        dispatch(deleteApplicant(id))
    }

    // ĞÑĞ±Ğ¾Ñ Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»Ñ, ĞµÑĞ»Ğ¸ ĞµÑÑÑ ÑĞ´Ğ°Ğ»ÑĞµĞ¼, ĞµÑĞ»Ğ¸ Ğ½ĞµÑ Ğ¿ÑÑĞ¸Ğ¼ Ğ² Ğ¼Ğ°ÑÑĞ¸Ğ²
    const handleCheck = (e, index) => {
        if (!checkIndex.includes(index)) {
            checkIndex.push(index)
        }
        else {
            const elemIndex = checkIndex.indexOf(index)
            checkIndex.splice(elemIndex, 1)
        }
        dispatch(arrayIndexApplicantAction(checkIndex))
    }

    return (
        <div className="wrap applicant">
            <h2 className={styles.title}>ĞĞ°ÑĞ²Ğ½Ğ¸ĞºĞ¸</h2>

            <div className="wrap-checkBox">
                { applicants.map( (applicant, index) => {
                    return (
                        <FormControlLabel
                            key={applicant.id}
                            className={styles.root}
                            control={
                                <Checkbox
                                    onChange={e => handleCheck(e, index)}
                                    name="checkedB"
                                    color="primary"
                                />
                            }
                            label={
                                <>
                                    <strong>{applicant.name}</strong>,
                                    {applicant.address.address}
                                    <span
                                        onClick={_ => handleDelete(applicant.id, index)}
                                        style={{
                                            display: applicant.delete
                                                ? 'inline-block'
                                                : 'none',
                                            marginLeft: '5px'
                                        }}
                                    >
                                       &#x2716;
                                    </span>
                                </>
                            }
                        />
                    )
                })}
            </div>

            <AddApplicant/>

            <div style={{ display : client ? 'block' : 'none'}}
                 className="button-wrap"
            >
                <Submit />
            </div>

        </div>
    );
};

export default Applicants;
