import {
    addApplicantData, arrayIndexApplicant,
    client,
    delApplicant,
    getApplicants,
    getClients, jsonDATA
} from "./constants";


const initialState = {
    getClients: [],
    getApplicants : [],
    client : null,
    data : {
        id : '',
        applicantsIds : [],
        newApplicants : []
    },
    jsonDATA : ''
}

export const reducer = (state = initialState, action) => {

    switch (action.type) {

        case getClients :

            return {
                ...state,
                getClients : action.clients
            }

        case client :

            return {
                ...state,
                client : action.clientObj
            }

        case getApplicants :

            return {
                ...state,
                getApplicants : action.applicants
            }

        case addApplicantData :

            return {
                ...state,
                getApplicants : [...state.getApplicants, action.data]
            }

        case delApplicant :

            return {
                ...state,
                getApplicants : state.getApplicants
                                .filter(applicant => applicant.id !== action.id)
            }

        // Ğ¤Ğ¾ÑĞ¼Ğ¸ÑĞ¾Ğ²Ğ°Ğ½Ğ¸Ğµ Ğ¾Ğ±ÑĞºÑĞ° Json
        case arrayIndexApplicant :

            // ĞĞ¾Ğ»ÑÑĞ°ĞµĞ¼ Ğ²ÑĞµÑ Ğ¿Ğ¾Ğ»ÑĞ·Ğ¾Ğ²Ğ°ÑĞµĞ»ĞµĞ¹ Ğ¿Ğ¾ Ğ¸Ğ½Ğ´ĞµĞºÑĞ°Ğ¼
            const applicantsIds = action.data.reduce((acc, itemIndex) => {
                acc.push(state.getApplicants[itemIndex])
                return acc
            }, [])

            // ĞÑĞ»Ğ¸ Ñ Ğ·Ğ°ÑĞ²Ğ¸ÑĞµĞ»Ñ ĞµÑÑÑ ÑĞ²Ğ¾Ğ¹ÑÑĞ²Ğ¾ delete ÑĞ¾ Ğ´Ğ¾Ğ±Ğ°Ğ²Ğ»ĞµĞ¼ Ğ² Ğ½Ğ¾Ğ²ÑĞ¹ Ğ¼Ğ°ÑÑĞ¸Ğ²
            const newApplicants = applicantsIds.filter(item => item?.delete)

            return {
                ...state,
                data : {
                    id : state.client?.id,
                    applicantsIds,
                    newApplicants
                }
            }

        case jsonDATA :

            return {
                ...state,
                jsonDATA: action.data
            }

        default :
            return {
                ...state
            }
    }
}
